/*                        nonmodal - sample                         */

.notification {
  visibility: hidden;
  min-width: 250px;
  background-color: #333;
  color: #fff;
  text-align: center;
  border-radius: 2px;
  padding: 16px;
  position: fixed;
  z-index: 1;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
}
.notification.show {
  visibility: visible;
  animation: fadein 0.5s, fadeout 0.5s 2.5s;
}
@keyframes fadein {
  from {bottom: 0; opacity: 0;}
  to {bottom: 30px; opacity: 1;}
}
@keyframes fadeout {
  from {bottom: 30px; opacity: 1;}
  to {bottom: 0; opacity: 0;}
}

.board-row {
  border-bottom: 1px solid #dee2e6; /* 원하는 보더 색상으로 변경할 수 있습니다. */
  padding-bottom: 8px; /* 보더와 컨텐츠 사이에 약간의 간격을 추가할 수 있습니다. */
  margin-bottom: 8px; /* 행 사이에 간격을 추가합니다. */
}

